@import '../../global.scss';



.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;


    h1{
        font-size: 50px;

        @include mobile{
            font-size: 20px;
          }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            margin: 10px 0;
           flex-wrap: wrap;
           justify-content: center;

          }

        // li{
        //     font-size: 14px;
        //     margin-right: 50px;
        //     padding: 7px;
        //     border-radius: 10px;
        //     cursor: pointer;

        //     &.active{
        //         background-color: $mainColor;
        //         color: white;
        //     }
        // }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
          }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid rgb(240, 239, 239);
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease ;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
              }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}
@media screen and (max-width: 450px) {
    .portfolio{
        padding: 6rem 0 0;
    }
    .portfolio ul{
        margin: 1.5rem 0 1.5rem;
    }
    .portfolio .container .item {
        width: 145px;
        height: 104px;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
    }
}